import React from "react";
import Image from "next/image";

function HeroSection() {
  return (
    <div className="my-[100px] flex items-center justify-center">
      <div className="flex items-center justify-center gap-[60px] lg:h-[290px] flex-col">
        <div className="flex h-[175px] w-auto flex-col items-center justify-center gap-5">
          {" "}
          <div className="relative flex h-[90px] items-center justify-center gap-[10px] rounded-xl bg-white lg:py-[14px] lg:pl-[20px] lg:pr-[30px]">
            {" "}
            <Image
              src={"/assets/hero_shine.png"}
              width={39}
              height={43}
              alt="hero_shine logo"
              className="absolute left-0 top-0"
            />
            <div className="flex items-center justify-center rounded-lg bg-primary lg:size-[62px]">
              <Image
                src={"/assets/logo_container.png"}
                width={34}
                height={34}
                alt="hero section logo"
                className="object-fill"
              />{" "}
            </div>
            <h1 className="flex h-auto items-center justify-center font-semibold lg:text-5xl">
              <span className="text-primary">Unlock </span> Your Creative
              Potential
            </h1>
          </div>
          <div className="flex h-[65px] flex-col items-center justify-center gap-[10px]">
            <h2 className=" font-bold lg:text-3xl">
              With Online Design and Development Courses.
            </h2>
            <p className="text-base text-gray-500">
              Learn from industry Experts and Enhance your skiis
            </p>
          </div>
        </div>
        <div className="flex h-[45px] items-center gap-5 lg:h-[55px]">
        <Link
          href="/signup"
          className={cn(
            buttonVariants({
              variant: `${path.includes("/signup") ? "default" : "ghost"}`,
            }),
            "h-[45px] w-[75px] px-6 md:w-auto md:py-3 md:text-sm lg:h-[55px] lg:py-[14px] lg:text-lg",
          )}
        >
          Sign Up
        </Link>

        <Link
          href="/login"
          className={cn(
            buttonVariants({
              variant: `${
                path.includes("/login") || !path.includes("/signup")
                  ? "default"
                  : "ghost"
              }`,
            }),
            "h-[45px] w-auto px-6 md:py-3 md:text-sm lg:h-[55px] lg:py-[14px] lg:text-lg",
          )}
        >
          Login
        </Link>
        <MobileNavBar />
      </div>
      </div>
    </div>
  );
}

export default HeroSection;
